* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  position: relative;
  background: linear-gradient(45deg, #FF2E63, #bd2eff, #FF2E63);
  background-size: 1000%;
  animation: bg 10s ease-in-out infinite;

  height: 100vh;
  /* Высота контейнера */
  overflow-y: scroll;
  /* Включаем вертикальную прокрутку для всего сайта */
  scroll-snap-type: y mandatory;
  /* Прокрутка по вертикали с "прилипанием" к секциям */
  -webkit-scroll-snap-type: y mandatory;
  will-change: background-position;
}

.App-header {
  background: rgba(234, 234, 234, .9);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  position: fixed;
  width: 100%;
  z-index: 100;
  transition: background .2s ease-in-out;
}

.App-header:hover {
  background: rgb(234, 234, 234);
}

section,
footer {
  scroll-snap-align: start;
  -webkit-scroll-snap-align: start;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

.App-header .container {
  display: flex;
  padding: 8px 0;
  justify-content: space-between;
  align-items: center;
}

.page-title {
  margin-right: auto;
  margin-left: 40px;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

.App-logo {
  max-width: 32px;
}

.Header-list {
  display: flex;
  list-style: none;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}

.Header-list__item {
  position: relative;
}

.Header-list__item a {
  text-decoration: none;
  font-size: 14px;
  color: #000;
  transition: color .2s ease-in-out;
}

.Header-list__item:hover a {
  color: #FF2E63;
}

.Header-action {
  background: #FF2E63;
  border: none;
  padding: 8px 20px;
  color: #fff;
  font-size: 14px;
  border-radius: 8px;
  cursor: pointer;
}

.App-herosection {
  height: 100dvh;
  padding-top: 70px;
  min-height: 100vh;
}

.App-herosection .container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 80px;
}

@keyframes bg {
  from {
    background-position: left bottom;
  }

  to {
    background-position: right top;
  }
}

h1 {
  font-size: 100px;
  color: #fff;
}

.cta-button {
  color: #fff;
  font-size: 80px;
  cursor: pointer;
  border: none;
  padding: 12px 16px;
  border-radius: 16px;
  background: linear-gradient(-45deg, #ffffff00, #FF2E63);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  background-size: 400%;
  background-position: left;
  animation: cta-button .5s ease-out forwards;
  transition: background-position 1s ease-in-out, outline .2s ease-in-out;
  will-change: transform, background-position;
}

@keyframes cta-button {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

.cta-button:hover {
  background-position: right;
  outline: 4px solid #fff;
}

.App-advantages {
  overflow: hidden;
  height: 100dvh;
  min-height: 100vh;
}

.App-advantages .container {
  height: 100%;
  display: flex;
  align-items: center;
}

.App-advantages__items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.App-advantages__items--item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px 28px;
  gap: 20px;
  font-size: 20px;
  text-align: center;
  background: rgba(255, 255, 255, .9);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 24px;
  animation: item-show 1s ease-in-out;
}

.App-advantages__items--item:hover {
  background: #fff;
}

.App-advantages__items--item p:nth-child(2) {
  font-weight: bold;
}

.App-advantages__items--item:nth-child(2) {
  animation-delay: .1s;
}

.App-advantages__items--item:nth-child(3) {
  animation-delay: .2s;
}

.App-advantages__items--item:nth-child(4) {
  animation-delay: .3s;
}

.App-advantages__items--item p:nth-child(1) {
  font-size: 100px;
}

@keyframes item-show {
  from {
    transform: translateY(-100%);
  }

  to {
    transform: translateY(0);
  }
}

.App-howitworks {
  height: 100dvh;
  min-height: 100vh;
}

.App-howitworks .container {
  height: 100%;
  display: flex;
  align-items: center;
}

.App-howitworks__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.App-howitworks__items--item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px 28px;
  gap: 20px;
  font-size: 20px;
  text-align: center;
  background: rgba(255, 255, 255, .9);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 24px;
}

.App-howitworks__items--item:nth-child(1) {
  animation: item1 1s ease-in-out forwards;
  z-index: 3;
  animation-delay: .3s;
}

.App-howitworks__items--item:nth-child(2) {
  animation: item2 1s ease-in-out forwards;
  transform: translate3d(-100%, 0, 0);
  z-index: 2;
  animation-delay: .5s;
  opacity: 0;
}

.App-howitworks__items--item:nth-child(3) {
  animation: item3 .9s ease-in-out forwards;
  transform: translate3d(-200%, 0, 0);
  z-index: 1;
  animation-delay: .5s;
  opacity: 0;
}

@keyframes item1 {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: perspective(500px) rotateY(-45deg);
  }

  100% {
    transform: translate(0, 0);
  }
}

@keyframes item2 {
  0% {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }

  50% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0%);
  }
}

@keyframes item3 {
  0% {
    opacity: 0;
    transform: translate3d(-200%, 0, 0);
  }

  50% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

.App-howitworks__items--item p:nth-child(2) {
  font-weight: bold;
}

.App-footer {
  background: #111;
}

.App-footer .container img {
  width: 100px;
}

.App-footer .container svg {
  fill: white;
}